import xweb from '@xweb/xweb-next';
import { useRouterHistory } from '@xweb/xweb-react-router-3/router';
import { createHistory } from '@xweb/xweb-react-router-3/history';
import './index.css';
import example from './models/example';
import router from './router';

const useHistory = useRouterHistory(createHistory)({
  basename: !window.___INITIAL_ENV__.baseRoot ? '/dist' : window.___INITIAL_ENV__.baseRoot
});
const app = xweb({
  history: useHistory
});

app.model(example);

app.router(router);

app.start('#react-content');



